.select2-selection--single {
    box-sizing: border-box;

    cursor: pointer;
    display: block;

    user-select: none;
    -webkit-user-select: none;

    .select2-selection__rendered {
        display: block;
        padding-left: 8px;
        padding-right: 20px;

        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
    }

    .select2-selection__clear {
        position: relative;
    }
}

&[dir="rtl"] {
    .select2-selection--single {
        .select2-selection__rendered {
            padding-right: 8px;
            padding-left: 20px;
        }
    }
}
