.section p {
    @include spacing(padding-top, 1rem);
    @include spacing(padding-bottom, .5rem);
}

.icons {
    font-size: 2em;
    display: flex;
    flex-wrap: wrap;

    margin-top: -0.25em;
    margin-left: -0.25em;

    > * {
        margin-top: 0.25em;
        margin-left: 0.25em;
    }
}

.icon {
    line-height: 1;
}

.icons + .icons {
    margin-top: 1rem;
}

.pg-logo {
    overflow: hidden;
    text-indent: 100%;
    white-space: nowrap;
    display: inline-block;
    line-height: 0;
    vertical-align: top;


    &:after {
        content: "";
        display: block;
        padding-top: 25.87%;
        background: url(./../../../vendor/pg-pattern-library/assets/images/pg-logo-white.svg) no-repeat center center;
        background-size: 100%;
        background-size: contain;
    }
}

.pg-logo--white {

}

.site-logo .pg-logo {
    display: block;
    margin: calc( ( 3rem - 40px ) / 2) auto;
    margin-top: calc( .75rem + ( ( 3rem - 40px ) / 2 ));
}
